export default {
  name: 'radiusbtn',
  props: {
    val: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      isCheck: this.val
    }
  },
  watch: {
    val: {
      handler(newValue, oldValue) {
        this.isCheck = newValue
      },
      deep: true
    }
  },
  methods: {
    trigger() {
      this.isCheck = this.isCheck === 1 ? 0 : 1
      this.$emit('radio', this.isCheck)
    }
  }
}
